
      html, body {
      min-height: 100%;
      }
      body, div, form, input, select, textarea, p { 
      padding: 0;
      margin: 0;
      outline: none;
      font-family: Roboto, Arial, sans-serif;
      font-size: 14px;
      color: #666;
      line-height: 22px;
      }
      h1 {
      position: absolute;
      margin: 0;
      font-size: 32px;
      color: #fff;
      z-index: 2;
      }
      .testbox {
      display: flex;
      justify-content: center;
      align-items: center;
      height: inherit;
      padding: 20px;
      }
      form {
      width: 100%;
      padding: 20px;
      border-radius: 6px;
      background: #fff;
      box-shadow: 0 0 30px 0 #8ebf42; 
      }
      fieldset {
          margin: 10px 0;
      }

      .banner {
      position: relative;
      height: 210px;
      /* background-image: url("/uploads/media/default/0001/01/a3df023f124a8bec3b213347404fe0a7318161de.jpeg");   */
      background-size: cover;
      background-color: #9af80c;
      display: flex;
      justify-content: center;
      align-items: center;
      text-align: center;
      }
      .banner::after {
      content: "";
      background-color: rgba(0, 0, 0, 0.5); 
      position: absolute;
      width: 100%;
      height: 100%;
      }
      p.top-info {
      margin: 10px 0;
      }
      input, select, textarea {
      margin-bottom: 10px;
      border: 1px solid #ccc;
      border-radius: 3px;
      }
      input {
      width: calc(100% - 10px);
      padding: 5px;
      }
      select {
      width: 100%;
      padding: 7px 0;
      background: transparent;
      }
      textarea {
      width: calc(100% - 12px);
      padding: 5px;
      }
      .item:hover p, .item:hover i, .question:hover p, .question label:hover, input:hover::placeholder {
      color: #8ebf42;
      }
      .item input:hover, .item select:hover, .item textarea:hover {
      border: 1px solid transparent;
      box-shadow: 0 0 8px 0 #8ebf42;
      color: #8ebf42;
      }
      .item {
      position: relative;
      margin: 10px 0;
      }


      .date {
        display: none;
        }
      .item i  {
      position: absolute;
      font-size: 20px;
      color: #a9a9a9;
      }




      .item i {
      right: 2%;
      top: 30px;
      z-index: 1;
      }



      input[type=radio] {
      width: 0;
      visibility: hidden;
      }
      label.radio {
      position: relative;
      display: inline-block;
      margin: 5px 20px 5px 0;
      cursor: pointer;
      }
      .question span {
      margin-left: 30px;
      }
      label.radio:before {
      content: "";
      position: absolute;
      left: 0;
      width: 17px;
      height: 17px;
      border-radius: 50%;
      border: 2px solid #8ebf42;
      }
      label.radio:after {
      content: "";
      position: absolute;
      width: 8px;
      height: 4px;
      top: 6px;
      left: 5px;
      background: transparent;
      border: 3px solid #8ebf42;
      border-top: none;
      border-right: none;
      transform: rotate(-45deg);
      opacity: 0;
      }
      input[type=radio]:checked + label:after {
      opacity: 1;
      }
      .btn-block {
      margin-top: 10px;
      text-align: center;
      }
      button {
      width: 150px;
      padding: 10px;
      border: none;
      border-radius: 5px; 
      background: #8ebf42;
      font-size: 16px;
      color: #fff;
      cursor: pointer;
      }
      button:hover {
      background: #82b534;
      }

      .overlay-modal {
          display: block;
          position: fixed;
          width: 100%;
          height: 100%;
          background-color: rgba(250, 250, 250, 0.5);
         overflow-y: auto;
         z-index: 1000;
         top: 0;
         left: 0;
         right: 0;
         bottom: 0;
         /* display: flex; */
         justify-content: center;
         align-items: center;
         height: inherit;
         padding: 20px;
      }

      .card {
        width: 75%;
        padding: 20px;
        border-radius: 6px;
        background: #fff;
        box-shadow: 0 0 30px 0 #8ebf42; 
        position: relative;
        margin: 20px auto;
        }
        .inner-element {
            position: relative;
        }

      .form-product {
          max-width: 580px;
          margin: 20px auto;
          z-index: 3;
      }

      .left {
          display: flex;
          flex-direction: column;
          align-items: flex-start;
      }

      .radioArea {
          display: flex;
          justify-content: space-between;
      }

      .question-block {
          width: 45%;
      }

      .date-now {
          font-size: 24px;
          margin: 15px 15px;
          font-weight: 700;
          text-align: right;
      }

      .line-questions {
          display: flex;
          justify-content: space-between;
      }

      .elements-area {
          display: flex;
          justify-content: space-around;
      }

      .column-check {
          display: flex;
          flex-direction: column;
          align-items: center;
          font-size: 12px;
          /* text-align: center; */
          margin: 3px;
          padding: 5px;
          border: 1px solid rgb(205, 207, 206);
          border-radius: 4px;
      }

      .column-check:hover {
        box-shadow: 0 0 30px 0 #8ebf42; 
      }

      .comments-area {
          /* list-style: none; */
          text-align: left;
          font-size: 12px;
      }

      .input-text {
          width: 85%;
          min-height: 80px;
          margin: 15px;
          border: 1px solid lightslategrey;
      }

      .table-header {
          color: white;

      }

      .close_icon
      {
        transition: all 0.075s ease-in-out;
        position: absolute;
        width: 2em;
        height: 2em;
        top: 30px;
        right: 15px;
        transform: translate(-50%,-50%);
        border: 0.1em solid;
        border-color: rgba(211, 10, 10, 0.8);
        border-radius: 6em;
        z-index: 3;
        /*background: rgba(255,255,255,0.8);*/
      }
      
      .close_icon:after
      {
        transition: all 0.075s ease-in-out;
        font: 1em Helvetica, arial;
        font-size: em;
        width: 100%;
        height: 100%;
        content:"x";
        color: rgba(250,250,250,0.8);
        display: flex;
        justify-content: center;
        align-items: center;
        transform: translate(0,-5%) scale(1.25,1);
        color: red;
        
      }
      .close_icon:hover {
          border-color: salmon;
          
      }
      .warehouse-header {
          display: flex;
          justify-content: space-between;
          width: 94%;
          font-size: 12px;
          font-weight: 700;
          text-align: center;
      }

      .warehouse-items {
        display: flex;
        justify-content: space-between;
        width: 94%;
          font-size: 10px ;
      }
      .warehouse-items:hover {
          background-color: rgb(186, 190, 194);
      }


      .warehouse-name-header {
        width: 55%;
        text-align: center;
    }

      .warehouse-name {
          width: 55%;
          text-align: left;
      }

      .warehouse-text {
        font-size: 12px ;
      }

      .warehouse-numbers {
          width: 15%;
          font-size: 12px;
          text-align: center;
      }

      @media (min-width: 568px) {
      .name-item, .city-item {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      }
      .name-item input, .city-item input {
      width: calc(50% - 20px);
      }
      .city-item select {
      width: calc(50% - 8px);
      }
      }

